img {
  display: block;
  max-width: 100%;
  height: auto;
}
.hero-picture-block {
  display: block;
  position: relative;
  width: 100%;
  height: auto; 
}
.hero-picture {
  display: block;
}

.organic-food-icon {
  position: absolute;
  top: -10px;
  right: 15px;
}

  .hero {
    background-color: var(--primary-bg-color);
    padding-top: 37px;
    padding-bottom: 37px;
  }

  .hero-title {
    color: var(--title-black-text-color);
    text-align: center;
    font-family: 'Neue Montreal Medium';
    font-size: 44px;
    font-style: normal;
    font-weight: 500;
    line-height: 1.09;
    letter-spacing: -0.02em;
    width: 100%;
    margin: 0 auto 16px;
  }

  .hero-title-accent {
    color: var(--secondary-bg-green-color);
  }

  .hero-text {
    width: 100%;
    height: 55px;
    color: rgba(1, 1, 1, 0.8);
    text-align: center;
    font-family: 'Neue Montreal Medium';
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.29;
    letter-spacing: -0.01em;
    margin: 0 auto;
    margin-bottom: 31px;
  }

  @media only screen and (max-width: 768px) {
    .hero-img {
      width: 335px;
      height: 100%;
      margin: 0 auto;
    }
  }

@media only screen and (min-width: 768px) and (max-width: 1440px) {
  .hero {
    padding-top: 58px;
  }

  .hero-img {
    width: 704px;
    height: 100%;
  }

  .hero-title {
    font-size: 80px;
    width: 658px;
    height: 160px;
    margin-bottom: 20px;
  }

  .hero-text {
    width: 492px;
    height: 48px;
    font-size: 18px;
    font-style: normal;
    line-height: 1.3;
    letter-spacing: 0;
    margin-bottom: 40px;
  }

  .organic-food-icon {
    position: absolute;
    top: -30px;
    left: 594px;
  }
}

@media only screen and (min-width: 1440px) {
  .hero {
    padding-top: 39px;
  }

  .hero-img {
    width: 1240px;
    height: 100%;
  }

  .hero-title {
    font-size: 80px;
    margin-bottom: 20px;
    width: 1042px;
    height: 80px;
    letter-spacing: -0.022em;
  }

  .hero-text {
    font-size: 18px;
    width: 492px;
    height: 48px;
    margin-bottom: 40px;
  }

  .organic-food-icon {
    position: absolute;
    top: -50px;
    left: 1120px;
  }
}
