.footer-section {
    background-color:var(--secondary-bg-green-color);
    color:var(--primary-bg-color) ;
}
.footer-logo-wrapper{
    padding-top: 40px;
    padding-bottom: 20px;
}
.footer-logo-link{
    color: var(--primary-bg-color);
font-family: "Quincy CF";
font-size: 18px;
font-style: normal;
font-weight: 900;
line-height: 20px; 
letter-spacing: -0.36px;
display: flex;
gap: 8px;

align-items: center;
}
.footer-logo-icon{
    width: 28px;
    height: 28px;
    flex-shrink: 0;
   background-color: var(--primary-bg-color);
    border-radius: 50%;
     fill: var(--secondary-bg-green-color);
     padding: 6px; 
     transition: all 0.3s;
}
.footer-logo-icon:hover,
.footer-logo-icon:focus{
    fill:var(--primary-bg-color);
    background-color:var(--secondary-bg-green-color) ;
    border: 1px solid var(--primary-bg-color);
}
.footer-soc-wrapper{
  padding-bottom: 40px;  
}
.footer-soc-list {
    display: flex;
    gap: 12px;
}
.footer-soc-item {
}

.footer-soc-svg {
    fill: var(--primary-bg-color);
    padding: 8px;
    border-radius: 10px;
    border: 1px solid rgba(232, 232, 226, 0.30);
}
.footer-soc-svg:hover,
.footer-soc-svg:focus {
    background: rgba(232, 232, 226, 0.20);
    border-radius: 10px;
}

.footer-title {
font-size: 24px;
font-weight: 500;
line-height: 1.2;
margin-bottom: 14px;
}
.footer-text-wrapper{
    padding-bottom: 80px;
}
.footer-text {
    color: rgba(232, 232, 226, 0.70);
    font-size: 14px;
    font-weight: 400;
    line-height: 1.29; /* 128.571% */
}
.footer-form-wrapper{
    margin-bottom: 80px;
}
.footer-form-title {
    color:var(--primary-bg-color);
    font-size: 14px;
    font-weight: 400;
    line-height: 1.29; /* 128.571% */
    margin-bottom: 20px;
}
.footer-form-label {
}

.footer-form-input {
    padding: 12px 18px;
    border-radius: 30px;
    border: 1px solid var(--primary-bg-color);
    width: 100%;
    margin-bottom: 14px;
    background-color: transparent;
    
}
.footer-form-input::placeholder {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.29; 
    color: rgba(232, 232, 226, 0.60); 
    font-size: 16px;
    font-family: 'Neue Montreal Regular';
  }
.footer-form-btn{
    color:var(--title-black-text-color);
    font-size: 14px;
    font-weight: 500;
    line-height: 1.29; /* 128.571% */
    padding: 12px 149px;
    border-radius: 30px;
    background-color:var(--primary-bg-color);
    border: none;
    width: 100%;
}
.footer-form-btn:hover,
.footer-form-btn:focus{
    color: #6D8434;
}
.footer-privacy-wrapper{
    padding-bottom: 80px;
}
.footer-subtitle{
font-size: 14px;
font-weight: 400;
line-height: 1.29; /* 128.571% */
margin-bottom: 8px;
}
.footer-privacy {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.29;
    margin-right: 8px;
    display: inline-block;
    transition: all 0.3s;
    
}
.footer-privacy:hover,
.footer-privacy:focus{
    transform: scale(1.1);
}
.footer-span {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.29;
    margin-right: 8px;
}
.footer-terms {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.29;
    display: inline-block;
    transition: all 0.3s;
    
}
.footer-terms:hover,
.footer-terms:focus{
    transform: scale(1.1);
}
