  top: 50%;
  right: -2px;
  transform: translate(-50%, -50%);
}

.filters-down-svg {
  fill: var(--primary-bg-color);
  stroke: var(--title-black-text-color);
  transition: all .3s;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translate(-50%, -50%);
}

.rotate {
  transform: translate(-50%, -50%)rotate(180deg);
}

.filters-categories-list, .filters-allTypes-list {
  z-index: 2;
  width: 100%;
  visibility: hidden;
  opacity: 0;
  background: #fff;
  border-radius: 15px;
  padding: 14px 20px;
  transition: all .3s;
  position: absolute;
  top: 56px;
  left: 0;
  box-shadow: 0 4px 11.2125px rgba(0, 0, 0, .04), 0 20px 69px rgba(0, 0, 0, .07);
}

.filters-allTypes-list {
