body {
  background: var(--primary-bg-color);
}

.section-cart {
  padding-top: 37px;
  padding-bottom: 80px;
}

.cart-title {
  color: var(--title-black-text-color);
  text-transform: uppercase;
  align-items: center;
  margin-bottom: 14px;
  font-family: Neue Montreal Medium;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.16;
  display: flex;
}

.cart-icon-shopping {
  background: var(--secondary-bg-green-color);
  fill: var(--primary-bg-color);
  width: 38px;
  height: 38px;
  border-radius: 50%;
  margin-right: 14px;
  padding: 10px;
}

.delete-all-box {
  justify-content: end;
  align-items: center;
  gap: 8px;
  margin-bottom: 10px;
  padding-right: 7px;
  display: flex;
}

.delete-all-text {
  color: var(--title-black-text-color);
  font-family: Neue Montreal Medium;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
}

.delete-all-icon {
  fill: var(--title-black-text-color);
}

.cart-content, .cart-shopping-list {
  flex-direction: column;
  display: flex;
}

.cart-shopping-item {
  padding: 30px 0;
  display: flex;
}

.cart-shopping-item:not(:last-child) {
  border-bottom: 1px solid var(--secondary-text-color);
}

.cart-shopping-img {
  width: 100px;
  height: 100px;
  background: var(--card-bg-color);
  border-radius: 10px;
  margin-right: 20px;
}

.cart-item-info {
  width: 100%;
  flex-direction: column;
  display: flex;
}

.cart-info-top {
  justify-content: space-between;
  margin-bottom: 10px;
  display: flex;
  position: relative;
}

.cart-delete-icon {
  width: 18px;
  height: 18px;
  position: absolute;
  top: -8px;
  right: 0;
}

.cart-info-name-product {
  color: var(--title-black-text-color);
  font-family: Neue Montreal Medium;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

.cart-properties-list {
  gap: 8px;
  margin-bottom: 36px;
  display: flex;
}

.cart-properties-item {
  align-items: baseline;
  gap: 4px;
  display: flex;
}

.cart-properties-caption {
  color: var(--secondary-text-color);
  font-family: Neue Montreal Regular;
  font-size: 12px;
  line-height: 1.16;
}

.cart-properties-subtitle {
  color: var(--title-black-text-color);
  font-family: Neue Montreal Regular;
  font-size: 12px;
  line-height: 1.16;
}

.cart-info-bottom {
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
}

.cart-info-price {
  color: var(--title-black-text-color);
  font-family: Neue Montreal Medium;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

.cart-counter {
  border: 1px solid var(--secondary-text-color);
  width: 74px;
  height: 26px;
  border-radius: 100px;
  justify-content: center;
  align-items: center;
  padding: 4px;
  display: flex;
}

.cart-counter-value {
